
import { defineComponent, ref, onMounted, PropType } from 'vue';
import { getPokemonByName } from '@/api/pokemonAPI';
import { capitalize } from '@/helpers';

interface PokemonDetails {
  name: string;
  types: Array<{ type: { name: string } }>;
  stats: Array<{ stat: { name: string }, base_stat: number, effort: number }>;
  sprites: { front_default: string };
}

export default defineComponent({
  props: {
    name: String as PropType<string>,
  },
  setup(props) {
    const pokemonDetails = ref<PokemonDetails | null>(null);
    const loading = ref(true);
    const errorMessage = ref<string | null>(null);

    onMounted(async () => {
      if (props.name) {
        try {
          pokemonDetails.value = await getPokemonByName(props.name);
        } catch (error) {
          console.error("Failed to fetch Pokémon details:", error);
          errorMessage.value = "Failed to load Pokémon details. Please try again later.";
        } finally {
          loading.value = false;
        }
      } else {
        errorMessage.value = "Pokémon name is not provided.";
        loading.value = false;
      }
    });

    return {
      pokemonDetails,
      loading,
      errorMessage,
      capitalize
    };
  }
});
